





















































import common from '../../common/common.js';
import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			baseurl: "",
			itemList: [],
			itemList1: [],
		}
	},
	computed: {
		...mapState(['user']),
	},
	methods: {
		islogin: function() {
			if (this.user.user === null) {
				uni.reLaunch({
					url: '../../pages/manage/login'
				})
				return;
			}
		},
		msglist: function() {
			uni.navigateTo({
				"url": "../../pages/train/msglist"
			})
		},
		category: function(cat) {
			uni.navigateTo({
				url: "../../pages/train/tranInfo?lifecat=" + JSON.stringify(cat)
			})
		},
		getflash: function() {

			let that = this;
			//console.log(this.user.user.token);
			uni.request({
				url: common.hosturl + "index/getflash.html",
				method: "POST",
				data: {
					token: this.user.user.token,
					flashcat: 4
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					console.log(JSON.stringify(res.data));
					if (res.data.code == 0) {
						that.itemList = res.data.list;
						that.baseurl = res.data.baseurl;

					} else {
						uni.showToast({
							icon: 'none',
							title: '出错啦，请联系客服'
						});
					}
				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}
			})



		},
		getpush: function() {

			let that = this;
			//console.log(this.user.user.token);
			uni.request({
				url: common.hosturl + "index/toppushlist.html",
				method: "POST",
				data: {
					token: this.user.user.token,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					console.log(JSON.stringify(res.data));
					if (res.data.code == 0) {
						that.itemList1 = res.data.list;
						//that.baseurl = res.data.baseurl;

					} else {
						uni.showToast({
							icon: 'none',
							title: '出错啦，请联系客服'
						});
					}
				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}
			})



		}

	},
	onLoad: function() {
		this.islogin();


	},
	onShow: function() {
		this.getflash();
		this.getpush();
	}
}



